/** This file contains all the css for the site */

/* Styling for the div containing the dark mode button */
.toggle-div {
    margin-left:    auto;
    height:         15px;
    float: right;
}

/* Dark mode styling for the elements */
body.darkmode {
    background-color:   #36454F;
    color:              #C8C8C8;
}

body.darkmode .readmore-btn {
    background: #10101c;
    color: #C8C8C8;
    border: 1px solid #002e43;
    border-radius:0.5rem;
}

body.darkmode .social-btn {
    background: transparent;
    color: #ffffff;
    border: none; /* Remove borders */
    border-radius:0.0rem;
}

body.darkmode a { 
    text-decoration: none;
    color: #C8C8C8;
}

body.darkmode .post-p {
    color: #E8E8E8;
}

body.darkmode .postlink {
    color: #088F8F;
}

body.darkmode a {
    text-decoration: underline;
}

body.darkmode a:hover {
    color: #7B68EE;
}

body.darkmode .pagination a {
    color: black;
}


/* General styling */

body {
    font-family: "Times New Roman", Times, serif;
    font-size: 15px;
}

/* Center content and justify to the left */
.div-wrap {
    text-align:         center;
}

.div-content {
    display:            inline-block;
    text-align:         left;
    justify-content:    center;
    flex-direction:     column;
    //inline-size:        500px;
    overflow:           hidden;
    font-family:        "Times New Roman", Times, serif;
    font-size:          15px;
}

h1 {
    font-family:        "Times New Roman", Times, serif;
    font-size: 20px;
}

/* Blog post styling */

#li_size {
    font-size: 20px;
}

.postlink {
    color: #088F8F; // pastel blue
    font-size: 30px;
}

.post-p {
    font-size: 18px;
    color: #686868;
}

.div-justify {
    margin:     auto;
    width:      70%;
    padding:    10px;
}

/* Pagination styling */

//.pagination a, .pagination span {
.pagination a, .pagination span {
    padding: 7px 18px;
    border: 3px solid #eee;
    margin-left: -2px;
    margin-right: -2px;
    background-color: #ffffff;
    display: inline-block;
    color: black;
  }

/* -------------------- Styling for navabar (included in almost each page) -------------------- */


// Add a black background color
.topnav {
  overflow: hidden;
  font-family: Times New Roman;
}

// Style the links inside the navigation bar
.topnav a {
    float: left;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 14px;
    border-radius: 20px;
}

// Change the color of links on hover
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

// Add a color to the active/current link
.topnav a.active {
  background-color: #0099ff;
  color: black;
}

.topnav .icon {
    display: none;
}

.topnav_right{
    float:right;
}

// Social buttons inside the navbar
.social-btn {
  background-color: #ffffff;
  border: none; /* Remove borders */
  color: black; /* White text */
}

.readmore-btn {
    color: black;
    background-color: #9FE2BF;
    border-radius:  8px;
    padding: 4px 4px;
    font-size:  18px;
}

@media screen and (max-width: 600px) {
    .topnav a {display: none}
    .topnav_right{float: left;}
    .topnav a.icon {
        float: left;
        display: block;
    }
}


/* The "responsive" class is added to the topnav with JavaScript when the user clicks on the icon. This class makes the topnav look good on small screens (display the links vertically instead of horizontally) */
@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive a.icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
}

.float-container {
    display: flex;
    width: 100%;
}

.float-child {
    margin: 5px;
    padding-right: 50px;
    float: left;
}

.my_img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 50%;
}

.contact-a {
    text-decoration: underline;
    color:#0099ff;
}

.contact-a:visited {
    color:#0099ff;
}

.ul-no-bullets {
    list-style: none;
}

.div-center {
    text-align: center;
}

.footer {
    margin-top: 150px;
}

.responsive-img {
  max-width: 100%;
  height: auto;
}
